@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
body {
  background-color: #edb5f9;
  font-family: Roboto, sans-serif;
  position: relative;
}

.chat_form {
  background-color: #fff;
  flex-direction: column;
  padding: 8px;
  display: flex;
}

.top {
  height: 32px;
  width: 100%;
  justify-content: space-between;
  display: flex;
}

#settings, #autorization, #confirmation {
  width: auto;
  border: 1px solid;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
}

#exit {
  width: 66px;
  border: 1px solid;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
}

.middle {
  height: calc(100vh - 160px);
  border: 1px solid;
  flex-direction: column;
  gap: 10px;
  margin: 8px 0;
  padding: 10px;
  display: flex;
  overflow-x: hidden;
  overflow-y: auto;
}

.outgoing_message {
  background-color: bisque;
  align-self: end;
}

.incoming_message {
  background-color: #ff8c00;
}

.outgoing_message, .incoming_message {
  width: -moz-fit-content;
  width: fit-content;
  max-width: 80%;
  min-width: 50px;
  padding: 10px 10px 20px;
  position: relative;
}

.message {
  width: 100%;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.time {
  position: absolute;
  bottom: 2px;
  right: 4px;
}

.outgoing_message .time {
  color: #a9a9a9;
}

.incoming_message .time {
  color: #fff;
}

.bottom {
  height: 32px;
  justify-content: space-between;
  gap: 10px;
  display: flex;
}

#enter_message {
  width: 100%;
  border: 1px solid;
  padding: 8px;
}

#send_message {
  background: none;
  border: 1px solid;
  flex-shrink: 0;
  padding: 8px;
}

.modal {
  display: none;
}

.modal.active {
  background-color: gray;
  padding: 10px;
  display: flex;
  position: absolute;
  inset: 0;
}

.modal .card {
  height: auto;
  background: #fff;
  margin: auto;
  padding: 14px 20px;
}

.modal .card .top {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal .card .top h1 {
  font-size: 16px;
  font-weight: 600;
}

.modal .card .close {
  cursor: pointer;
  color: gray;
  background: #fff;
  border: none;
  padding: 2px;
  font-size: 32px;
}

.modal .card h2 {
  margin-top: 40px;
  font-size: 16px;
  font-weight: 500;
}

.modal .card form {
  flex-direction: column;
  gap: 10px;
  margin: 0;
  display: flex;
}

.modal .card form input {
  width: 245px;
  height: 32px;
  border: 1px solid #000;
  padding: 6px 7px 7px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 19px;
}

.modal .card form button, .button {
  width: 100%;
  background: #fff;
  border: 1px solid #000;
  margin-top: 5px;
  padding: 7px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.name {
  color: #00008b;
  margin-bottom: 5px;
  text-decoration: underline;
}

